// import new treatises here
import Zarlino_1588 from './Zarlino_1588.json'
import My_Treaty from './My_Treaty.json'
import Proetorius_1619 from './Praetorius_1619.json'
import Guillotel from './Guillotel.json'

// include treatiseIris in array
export const treatiseList = [Zarlino_1588.iri, My_Treaty.iri, Proetorius_1619.iri, Guillotel.iri]

// include treatise in object
const treatises = {
  [Zarlino_1588.iri]: Zarlino_1588,
  [My_Treaty.iri]: My_Treaty,
  [Proetorius_1619.iri]: Proetorius_1619,
  [Guillotel.iri]: Guillotel,
}

export const getTreatise = treatiseIri => treatises[treatiseIri]

export const getConceptLabel = conceptIri => {
  for (const treatiseIri of treatiseList) if (conceptIri.match(treatiseIri)) return conceptIri.slice(treatiseIri.length)
  return null
}

export const getTreatiseIri = conceptIri => {
  for (const treatiseIri of treatiseList) if (conceptIri.match(treatiseIri)) return treatiseIri
  return null
}
